"use client";

import React, { useState, useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';

// Bloom's Taxonomy levels
const BLOOM_LEVELS = [
  "Knowledge",
  "Comprehension",
  "Application",
  "Analysis",
  "Synthesis",
  "Evaluation"
];

// Sample exam questions with their Bloom's taxonomy classifications
const SAMPLE_QUESTIONS = {
  "Express the expected regret at time t= 10 as a function of the pis.": {
    bloom: "Comprehension",
    explanation: "This question requires understanding the concept of expected regret and applying it to a specific scenario."
  },
  "What is the probability of choosing arms 0, 1 or 2 at time 10?": {
    bloom: "Application",
    explanation: "The student must apply the ε-greedy algorithm to calculate specific probabilities."
  },
  "What will UCB choose at time 10?": {
    bloom: "Application",
    explanation: "This question requires applying the UCB formula to determine a specific outcome."
  },
  "What is limt→∞ Ni(t)/t? Use it to explain the fundamental difference between UCB and ε-greedy.": {
    bloom: "Analysis",
    explanation: "This question requires analyzing the limiting behavior of two algorithms and comparing their performance."
  },
  "What are the reward R(s,a) and the transition probabilities P(s′|s,a) for s= 12?": {
    bloom: "Application",
    explanation: "The student must apply the MDP framework to determine specific values."
  },
  "What is the corresponding Q-table, assuming that γ = 0.5?": {
    bloom: "Application",
    explanation: "The student must apply formulas to calculate Q-values."
  },
  "You perform one iteration of value iteration. Write down the table of V1(s).": {
    bloom: "Application",
    explanation: "This requires applying the value iteration algorithm to compute specific values."
  },
  "What is the optimal policy?": {
    bloom: "Evaluation",
    explanation: "The student must evaluate different policies to determine which is optimal."
  },
  "Recall the Q-learning update equation, and use it to explain how does Q-learning work.": {
    bloom: "Comprehension",
    explanation: "This tests understanding of the Q-learning algorithm and ability to explain it."
  },
  "Write down the value of the Q-table at the end of the episode.": {
    bloom: "Application",
    explanation: "The student must apply the Q-learning update formula to calculate values."
  },
  "Explain how to design an algorithm that computes the optimal solution.": {
    bloom: "Synthesis",
    explanation: "This requires creating a new algorithm for the given problem."
  },
  "If you do not know the probabilities, could you use a Q-learning approach? Explain how to do it.": {
    bloom: "Synthesis",
    explanation: "The student must create a Q-learning approach for this specific problem scenario."
  }
};

// Function to analyze a question
const analyzeQuestion = (text) => {
  const foundQuestion = Object.keys(SAMPLE_QUESTIONS).find(q => 
    text.toLowerCase().includes(q.toLowerCase()) ||
    q.toLowerCase().includes(text.toLowerCase())
  );
  
  if (foundQuestion) {
    return {
      bloom: SAMPLE_QUESTIONS[foundQuestion].bloom,
      explanation: SAMPLE_QUESTIONS[foundQuestion].explanation
    };
  }
  
  // Simple keyword-based fallback analysis
  const text_lower = text.toLowerCase();
  if (text_lower.includes("what is") || text_lower.includes("define") || text_lower.includes("identify")) {
    return { bloom: "Knowledge", explanation: "This question asks for basic recall of information." };
  } else if (text_lower.includes("explain") || text_lower.includes("describe") || text_lower.includes("summarize")) {
    return { bloom: "Comprehension", explanation: "This question requires understanding and explanation of concepts." };
  } else if (text_lower.includes("apply") || text_lower.includes("calculate") || text_lower.includes("solve")) {
    return { bloom: "Application", explanation: "This question requires applying knowledge to solve a problem." };
  } else if (text_lower.includes("analyze") || text_lower.includes("compare") || text_lower.includes("contrast")) {
    return { bloom: "Analysis", explanation: "This question requires breaking down information and making connections." };
  } else if (text_lower.includes("design") || text_lower.includes("create") || text_lower.includes("develop")) {
    return { bloom: "Synthesis", explanation: "This question requires creating something new from existing knowledge." };
  } else if (text_lower.includes("evaluate") || text_lower.includes("assess") || text_lower.includes("judge")) {
    return { bloom: "Evaluation", explanation: "This question requires making judgments based on criteria." };
  } else {
    return { bloom: "Application", explanation: "This appears to be an application-level question requiring problem-solving." };
  }
};

// Component definitions
const PreviewMessage = ({ message }) => {
  return (
    <div className={`p-4 rounded-lg max-w-3xl mx-auto w-full ${message.role === 'user' ? 'bg-blue-100' : 'bg-gray-100'}`}>
      <div className="font-bold">{message.role === 'user' ? 'Input' : 'Analysis'}</div>
      <div className="mt-1">{message.content}</div>
      {message.bloom_level && (
        <div className="mt-2 text-sm">
          <div className="font-semibold">Bloom's Taxonomy Level: <span className="font-bold text-blue-700">{message.bloom_level}</span></div>
          {message.explanation && (
            <div className="mt-1">{message.explanation}</div>
          )}
        </div>
      )}
    </div>
  );
};

const BloomReport = ({ questions }) => {
  const bloomCounts = {};
  BLOOM_LEVELS.forEach(level => {
    bloomCounts[level] = 0;
  });
  
  questions.forEach(msg => {
    if (msg.bloom_level) {
      bloomCounts[msg.bloom_level] = (bloomCounts[msg.bloom_level] || 0) + 1;
    }
  });
  
  const totalAnalyzed = Object.values(bloomCounts).reduce((sum, count) => sum + count, 0);
  
  if (totalAnalyzed === 0) return null;
  
  return (
    <div className="p-4 rounded-lg max-w-3xl mx-auto w-full bg-white shadow">
      <h3 className="font-bold text-xl mb-4">Bloom's Taxonomy Analysis Report</h3>
      <table className="min-w-full bg-white">
        <thead>
          <tr>
            <th className="py-2 px-4 border-b text-left">Bloom's Level</th>
            <th className="py-2 px-4 border-b text-left">Count</th>
            <th className="py-2 px-4 border-b text-left">Percentage</th>
          </tr>
        </thead>
        <tbody>
          {BLOOM_LEVELS.map(level => (
            <tr key={level}>
              <td className="py-2 px-4 border-b">{level}</td>
              <td className="py-2 px-4 border-b">{bloomCounts[level]}</td>
              <td className="py-2 px-4 border-b">
                {totalAnalyzed > 0 ? `${((bloomCounts[level] / totalAnalyzed) * 100).toFixed(1)}%` : '0%'}
              </td>
            </tr>
          ))}
        </tbody>
        <tfoot>
          <tr className="bg-gray-50">
            <td className="py-2 px-4 border-b font-bold">Total</td>
            <td className="py-2 px-4 border-b font-bold">{totalAnalyzed}</td>
            <td className="py-2 px-4 border-b font-bold">100%</td>
          </tr>
        </tfoot>
      </table>
      <div className="mt-4 text-sm text-gray-600">
        <p>This report shows the distribution of questions across Bloom's Taxonomy levels.</p>
        <p>Higher-level thinking skills (Analysis, Synthesis, Evaluation) promote deeper learning.</p>
      </div>
    </div>
  );
};

const Header = () => {
  return (
    <header className="bg-white shadow p-4">
      <h1 className="text-xl font-bold">AIIMPACT Pedagogical Alignment Tool</h1>
    </header>
  );
};

// Main component
const AlignmentEvaluation = () => {
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showReport, setShowReport] = useState(false);
  const [pdfjsLoaded, setPdfjsLoaded] = useState(false);

  // Load pdf.js from CDN
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.9.359/pdf.min.js';
    script.async = true;
    script.onload = () => {
      window.pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/2.9.359/pdf.worker.min.js';
      setPdfjsLoaded(true);
    };
    script.onerror = () => {
      console.error('Failed to load pdf.js');
      setPdfjsLoaded(false);
    };
    document.body.appendChild(script);
    return () => {
      document.body.removeChild(script);
    };
  }, []);

  // Handle PDF upload and analysis
  const handlePDFUpload = async (e) => {
    if (!pdfjsLoaded) {
      alert('PDF processing library not loaded yet. Please try again.');
      return;
    }

    const file = e.target.files[0];
    if (!file || file.type !== 'application/pdf') {
      alert('Please upload a valid PDF file');
      return;
    }

    setIsLoading(true);
    try {
      const arrayBuffer = await file.arrayBuffer();
      const pdf = await window.pdfjsLib.getDocument(arrayBuffer).promise;
      let fullText = '';

      // Extract text from all pages
      for (let i = 1; i <= pdf.numPages; i++) {
        const page = await pdf.getPage(i);
        const textContent = await page.getTextContent();
        const pageText = textContent.items.map(item => item.str).join(' ');
        fullText += pageText + '\n';
      }

      // Extract questions (simple heuristic: lines ending with '?')
      const questions = fullText
        .split('\n')
        .map(line => line.trim())
        .filter(line => line.endsWith('?'));

      // If no questions found, try sample questions
      const finalQuestions = questions.length > 0 ? questions : Object.keys(SAMPLE_QUESTIONS);

      // Analyze questions
      const newMessages = [];
      finalQuestions.forEach(q => {
        const userMessage = { content: q, role: 'user', id: uuidv4() };
        const analysis = analyzeQuestion(q);
        const responseMessage = {
          content: `This question aligns with the ${analysis.bloom} level of Bloom's Taxonomy.`,
          role: 'assistant',
          id: uuidv4(),
          bloom_level: analysis.bloom,
          explanation: analysis.explanation,
        };
        newMessages.push(userMessage, responseMessage);
      });

      setMessages(newMessages);
      setShowReport(true);
    } catch (error) {
      console.error('PDF processing error:', error);
      alert('Failed to process PDF');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="flex flex-col min-w-0 h-screen bg-gray-50">
      <Header />
      <div className="flex flex-col min-w-0 gap-6 flex-1 overflow-y-auto pt-4">
        {messages.length === 0 && (
          <div className="text-center p-4 max-w-3xl mx-auto">
            <h2 className="text-2xl font-bold mb-4">Pedagogical Alignment Evaluation</h2>
            <p className="mb-4">This tool analyzes exam questions according to Bloom's Taxonomy of Educational Objectives.</p>
            <div className="bg-white shadow rounded-lg p-4 text-left">
              <h3 className="font-bold mb-2">Bloom's Taxonomy Levels:</h3>
              <ul className="list-disc pl-5">
                {BLOOM_LEVELS.map(level => (
                  <li key={level}>
                    <strong>{level}:</strong> {level === "Knowledge" ? "Recall facts and basic concepts" : level === "Comprehension" ? "Understand and explain ideas" : level === "Application" ? "Apply knowledge in new situations" : level === "Analysis" ? "Draw connections among ideas" : level === "Synthesis" ? "Create new patterns or structures" : "Make judgments based on criteria"}
                  </li>
                ))}
              </ul>
              <p className="mt-2 text-sm text-gray-600">Upload a PDF exam to analyze its alignment with Bloom's taxonomy.</p>
            </div>
          </div>
        )}
        
        {showReport && messages.length > 0 && <BloomReport questions={messages.filter(m => m.role === 'assistant')} />}
        
        {messages.map((msg) => (
          <PreviewMessage key={msg.id} message={msg} />
        ))}
        
        {isLoading && (
          <div className="p-4 rounded-lg max-w-3xl mx-auto w-full bg-gray-100">
            <div className="font-bold">Analysis</div>
            <div className="mt-1">Processing PDF...</div>
          </div>
        )}
      </div>
      
      <div className="flex flex-col mx-auto px-4 pb-4 md:pb-6 gap-4 w-full md:max-w-3xl items-center">
        <div className="w-full p-4 bg-gray-50 rounded-lg">
          <h3 className="font-bold mb-2">Upload Exam PDF</h3>
          <input
            type="file"
            accept="application/pdf"
            onChange={handlePDFUpload}
            className="w-full border rounded p-2 mb-2"
            disabled={isLoading || !pdfjsLoaded}
          />
        </div>
        
        {messages.length > 0 && (
          <div className="flex gap-2">
            <button 
              onClick={() => setShowReport(!showReport)} 
              className="bg-gray-200 text-gray-800 rounded p-2 hover:bg-gray-300"
            >
              {showReport ? "Hide Report" : "Show Report"}
            </button>
            <button 
              onClick={() => {
                setMessages([]);
                setShowReport(false);
              }} 
              className="bg-red-100 text-red-800 rounded p-2 hover:bg-red-200"
            >
              Clear All
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default AlignmentEvaluation;